@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,400;0,500;0,600;1,300&display=swap);
.card {
  padding: 1rem;
  border-radius: 8px;
  background-color: var(--color-gray-three);
  border: 1px solid var(--color-gray-two);
  min-width: 16rem;
}

.card-title {
  font-size: 1.5rem;
}

.card-date {
  font-size: 0.825rem;
  color: var(--color-gray-four);
}

.options {
  position: absolute;
  text-align: start;
  top: 16rem;
  left: 2rem;
  z-index: 2;
  color: var(--color-white);
}

.options-desc {
  font-size: 0.825rem;
  color: var(--color-gray-four);
  top: 3rem;
  right: 2rem;
  position: absolute;
}

.options p:hover {
  text-decoration: underline;
}

.basic-geometry {
  width: 100%;
}

.options-active {
  text-decoration: underline;
  position: relative;
}

.options-color {
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 50%;
  margin-bottom: 0.75rem;
}

.example-list {
  flex: 2 1;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(16rem, 1fr));
  grid-gap: 1rem;
  gap: 1rem;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 1px 0;
  height: 5rem;
  background-color: var(--color-gray-three);
  border-bottom: 1px solid var(--color-gray-five);
  position: relative;
  padding: 1rem 2rem;
}

.header::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  bottom: 0;
  background-color: var(--color-black);
}

#home {
  flex: 1 1;
  background-image: radial-gradient(var(--color-gray-three) 1px, transparent 0);
  background-size: 1rem 1rem;
}

.home-content {
  padding: 2rem;
}

.txt-heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.5rem;
}

.txt-heading svg > path {
  stroke: var(--color-gray);
}

.examples-content {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
}

.about {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  min-width: 16rem;
}

.example-list-container {
  flex: 2 1;
}

.about-card {
  border-radius: 8px;
  padding: 1rem;
  background-color: var(--color-gray-three);
  border: 1px solid var(--color-gray-two);
}

.about-last-modified {
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  color: var(--color-primary);
}

.about-last-modified svg {
  width: 1rem;
  height: 1rem;
}

.about-contact-links {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.about-contact-link {
  display: flex;
  align-items: center;
  grid-gap: 0.25rem;
  gap: 0.25rem;
  color: var(--color-primary);
  text-decoration: underline;
  cursor: pointer;
}

.about-contact-link svg {
  width: 1rem;
  height: 1rem;
}

.about-contact-link svg > path {
  stroke: var(--color-primary);
}

.example-container {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 8px;
  position: relative;
  height: 100%;
  width: 100%;
  flex: 1 1;
  background-image: radial-gradient(var(--color-gray-three) 1px, transparent 0);
  background-size: 1rem 1rem;
}

.three-js-canvas {
  height: 100vh !important;
}

.example {
  width: 100%;
  position: relative;
}

.example-title {
  position: absolute;
  right: 0;
  top: -0.75rem;
  font-weight: 500;
  font-size: 1.75rem;
}

.example-txt {
  font-size: 1.125rem;
  position: fixed;
  top: 50%;
  left: calc(50% + 9rem);
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  z-index: 1;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  color: var(--color-gray-four);
}

.example-txt .icon-alert-error {
  width: 1.5rem;
  height: 1.5rem;
  margin: auto;
}

.nav-link-url-not-found {
  text-decoration: underline;
  color: var(--color-primary);
}

.theme-switch {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--color-gray-five);
  padding: 0.25rem;
  border-radius: 1rem;
  height: 2rem;
  position: relative;
  cursor: pointer;
}

.theme-switch-item {
  font-weight: 600;
  font-size: 0.925rem;
  padding: 0.25rem 1.75rem;
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  color: var(--color-gray-four);
  z-index: 2;
}

.theme-switch-item svg {
  fill: var(--color-gray-four);
  width: 1rem;
  height: 1rem;
}

.theme-switch-button {
  z-index: 1;
  width: 7.5rem;
  height: 1.5rem;
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  color: var(--color-white);
  background-color: var(--color-gray);
  border-radius: 1rem;
  background: linear-gradient(
    var(--color-gray) 80%,
    var(--color-gray-five) 120%
  );
  border-top: 1px solid var(--color-gray-four);
  transition: 0.25s;
}

.active.theme-switch-button {
  -webkit-transform: translateX(7.5rem);
          transform: translateX(7.5rem);
}

.active.theme-switch-item svg {
  fill: var(--color-white);
}

.active.theme-switch-item {
  color: var(--color-white);
}

.nav-menu {
  z-index: 10;
  padding: 1.25rem;
  background-color: var(--color-gray-three);
  border-right: 1px solid var(--color-gray-five);
  min-width: 18rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  transition: left 0.5s;
  overflow-y: auto;
}

.nav-menu::before {
  position: absolute;
  content: "";
  width: 1px;
  height: 100%;
  right: 0;
  top: 0;
  background-color: var(--color-black);
}

.nav svg > path {
  stroke: var(--color-gray);
}

.nav-header {
  display: flex;
  align-items: center;
  grid-gap: 0.75rem;
  gap: 0.75rem;
  margin: 0 0 1rem;
}

.nav-header-logo {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 12px;
  width: 2.75rem;
  height: 2.75rem;
  background-color: var(--color-white);
}

.nav-header-logo svg > path {
  stroke: var(--color-black);
}

.nav ul {
  margin: 1rem 0;
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-item {
  margin: 0.5rem 0;
}

.nav-link {
  display: flex;
  align-items: center;
  font-weight: 600;
  color: var(--color-gray-four);
  grid-gap: 0.5rem;
  gap: 0.5rem;
  border-radius: 8px;
  width: 100%;
  padding: 0.5rem 0.75rem;
}

.nav-link span {
  width: 100%;
}

.nav-link-active {
  color: var(--color-white);
  background-color: var(--color-gray-five);
}

.nav-link-active svg > path {
  stroke: var(--color-white);
}

.examples {
  padding-left: 2rem;
  position: relative;
}

.examples::before {
  position: absolute;
  content: "";
  left: 1.25rem;
  width: 1px;
  height: 91%;
  background-color: var(--color-gray);
}

.nav-link-example {
  width: calc(100% - 0.5rem);
  position: relative;
  margin-left: 0.5rem;
}

.nav-link-example::before {
  position: absolute;
  content: "";
  top: 0.25rem;
  left: -1.275rem;
  width: 1rem;
  height: 1rem;
  -webkit-transform: rotate(-50deg);
          transform: rotate(-50deg);
  border-radius: 50%;
  border-left: 1px solid var(--color-gray);
}

.nav-link-example::after {
  position: absolute;
  content: "";
  top: 1.0315rem;
  left: -0.625rem;
  width: 0.25rem;
  height: 0.25rem;
  border-radius: 50%;
  border: 1px solid var(--color-gray);
}

.nav-link-active.nav-link-example::after {
  background-color: var(--color-gray);
}

.nav-toggle {
  z-index: 10;
  bottom: 1rem;
  left: 1rem;
  position: fixed;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  background-color: var(--color-gray-five);
  background: radial-gradient(
    var(--color-gray-five) 66%,
    var(--color-gray-four)
  );
  box-shadow: 0 2px 10px var(--color-black-two);
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-toggle .icon-menu {
  width: 1.25rem;
  height: 1.25rem;
}

.nav-toggle .icon-menu > path {
  stroke: var(--color-white);
}

.nav-menu-open {
  left: 0 !important;
}

.nav-open {
  position: relative;
  z-index: 10;
}

.nav-open::after {
  position: absolute;
  content: "";
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: hsla(0, 0%, 10%, 0.7);
}

@media screen and (max-width: 956px) {
  .nav-menu {
    position: fixed;
    left: -100%;
  }
}

@media screen and (min-width: 957px) {
  .nav-toggle {
    display: none;
  }
}

.App {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.main {
  display: flex;
  height: 100%;
  width: 100%;
}

.main-content {
  flex: 1 1;
  overflow-y: auto;
  overflow-x: hidden;
}

.message-box {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: var(--color-black);
}

@media screen and (max-width: 1000px) {
  :root {
    --width-desktop: 40rem !important;
  }

  .home-scroll {
    display: none;
  }

  .footer-go-to-top {
    display: none !important;
  }

  .home-settings {
    margin-top: 1rem;
    position: static;
    position: initial;
  }

  .settings {
    flex-direction: row;
  }

  .footer-content-about {
    flex-direction: row !important;
  }

  .footer-content-about-txt {
    top: 2rem !important;
  }

  .pinpoint {
    margin: 1rem 0;
    right: unset !important;
    position: relative !important;
  }
}

:root {
  --color-hue: 160;
  --color-black: hsl(0, 0%, 7%);
  --color-black-two: hsl(0, 0%, 5%);
  --color-white: hsl(0, 0%, 100%);
  --color-gray: hsl(0, 0%, 30%);
  --color-gray-two: hsl(0, 0%, 11.5%);
  --color-gray-three: hsl(0, 0%, 10%);
  --color-gray-four: hsl(0, 0%, 50%);
  --color-gray-five: hsl(0, 0%, 15%);

  --color-primary: hsl(var(--color-hue), 50%, 62%);

  --font-body: "Nunito", sans-serif;
}

* {
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 4px;
}

::-webkit-scrollbar-thumb {
  background: hsl(0, 0%, 15%);
  background: var(--color-gray-five);
}

body {
  color: hsl(0, 0%, 100%);
  color: var(--color-white);
  background-color: hsl(0, 0%, 7%);
  background-color: var(--color-black);
  font-size: 1rem;
  font-family: "Nunito", sans-serif;
  font-family: var(--font-body);
  margin: 0;
  overflow-x: hidden;
}

body.day-mode {
  --color-black: hsl(0, 0%, 96%);
  --color-white: hsl(0, 0%, 14%);
  --color-gray: hsl(0, 0%, 65%);
  --color-gray-two: hsl(0, 0%, 90%);
  --color-gray-three: hsl(0, 0%, 91%);
  --color-gray-four: hsl(0, 0%, 55%);
  --color-gray-five: hsl(0, 0%, 85%);
  --color-primary: hsl(var(--color-hue), 60%, 50%);
  --color-black-two: hsl(0, 0%, 50%);
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1;
  color: hsl(0, 0%, 100%);
  color: var(--color-white);
  align-items: center;
}

p {
  margin: 0.5rem 0;
}

a {
  color: hsl(0, 0%, 100%);
  color: var(--color-white);
  text-decoration: none;
}

html {
  scroll-behavior: smooth !important;
}

hr {
  border: none;
  height: 2px;
  background-color: hsl(0, 0%, 15%);
  background-color: var(--color-gray-five);
  position: relative;
}

hr::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  top: 0;
  left: 0;
  background-color: hsl(0, 0%, 7%);
  background-color: var(--color-black);
}

