.example-container {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 8px;
  position: relative;
  height: 100%;
  width: 100%;
  flex: 1;
  background-image: radial-gradient(var(--color-gray-three) 1px, transparent 0);
  background-size: 1rem 1rem;
}

.three-js-canvas {
  height: 100vh !important;
}

.example {
  width: 100%;
  position: relative;
}

.example-title {
  position: absolute;
  right: 0;
  top: -0.75rem;
  font-weight: 500;
  font-size: 1.75rem;
}

.example-txt {
  font-size: 1.125rem;
  position: fixed;
  top: 50%;
  left: calc(50% + 9rem);
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  z-index: 1;
  gap: 0.5rem;
  color: var(--color-gray-four);
}

.example-txt .icon-alert-error {
  width: 1.5rem;
  height: 1.5rem;
  margin: auto;
}

.nav-link-url-not-found {
  text-decoration: underline;
  color: var(--color-primary);
}
